<template>
  <div id="signIn" style="height:100%;">
    <div
        class="flex-column flex-around flex-vertical"
        style="height:174px;background-color:#2ba06a;color:#fff;box-sizing:border-box;padding:50px 0;"
    >
        <p class="flex-vertical" style="height:30px;border-radius:15px;background-color:rgba(255,255,255,.3);padding:0 16px;font-size:12px;">
            <span v-if="currentDaySigninStatus">今日已签到</span>
            <span v-else @click="_signin">今日未签到</span>
        </p>
        <p>当月已连续签到{{currentMonthSigninList.length}}天</p>
    </div>

    <!-- 日历 -->
    <div style="height:calc(100% - 174px);">
        <van-calendar
            title="签到"
            :poppable="false"
            :show-confirm="false"
            :min-date="minDate"
            :max-date="maxDate"
            :formatter="calenderFormatter"
        />
    </div>
  </div>
</template>

<script>
import { getSigninRecordList, signin, getSigninStatus } from '@/api';
export default {
    data:()=>({
        date: new Date(),
        minDate: new Date(),
        maxDate: new Date(),
        currentMonthSigninList: [], // 当月签到情况数组
        currentDaySigninStatus: false, // 当日签到情况
    }),
    mounted(){
        this.initCalendar();
        this.getSigninStatus();
        this.getSigninRecordList();
    },
    methods: {
        /**
         * 初始化日期表
         */
        initCalendar(){
            let date = this.date;
            this.minDate = new Date(date.getFullYear(),date.getMonth(),1);
            this.maxDate = new Date(date.getFullYear(),date.getMonth()+1,0);
        },
        /**
         * 格式化日期文案
         */
        calenderFormatter(e){
            let year = e.date.getFullYear();
            let month = e.date.getMonth() + 1;
            let day = e.date.getDate();
            
            let find = this.currentMonthSigninList.find(v=>{
                let signDate = new Date(v.signTime)
                if(
                    year === signDate.getFullYear()
                    && month === (signDate.getMonth() + 1)
                    && day === signDate.getDate()
                ) return true;
                return false;
            })

            if(find) e.bottomInfo = '已签到'

            return e;
        },
        /**
         * 获取当日签到情况
         */
        getSigninStatus(){
            let date = this.date;
            getSigninStatus({
                date: date.getFullYear() + '-'
                    + ('0' + (date.getMonth() + 1)).substr(-2) + '-'
                    + ('0' + date.getDate()).substr(-2)
            }).then(res=>{
                if(res.code === 0){
                    this.currentDaySigninStatus = res.data.isSign;
                }else{
                    this.$toast.fail(res.msg);
                }
            })
        },
        /**
         * 获取当月签到情况
         */
        getSigninRecordList(){
            let date = this.date;
            getSigninRecordList({
                month: date.getFullYear()
                    + ('0' + (date.getMonth() + 1)).substr(-2)
            }).then(res=>{
                if(res.code === 0){
                    this.currentMonthSigninList = res.data.list;
                }else{
                    this.$toast.fail(res.msg);
                }
            })
        },
        /**
         * 签到
         */
        _signin(){
            signin({
                signIp: '',
                signGpxX: '',
                signGpxY: '',
                signAddr: '',
                signDevice: ''
            }).then(res=>{
                if(res.code === 0){
                    this.$toast.success('签到成功！');
                }else{
                    this.$toast.fail(res.msg);
                }
                this.getSigninStatus();
                this.getSigninRecordList();
            })
        }
    }
}
</script>


<style>
    #signIn .van-calendar__selected-day{
        background-color: #2ba06a !important;
    }
</style>